.container
    max-width: 120rem
    margin: 0 auto
    padding: 1.5rem

.header-title
    text-align: center
    margin-bottom: 4rem

.calc-box
    display: grid
    text-align: center
    justify-items: center

.calc-inputs
    display: grid
    gap: 2rem
    margin-bottom: 2rem
    justify-items: center

.input-a, .input-b
    display: flex
    & p
        margin-right: 1rem
    &__i
        margin-right: 1rem
    
.results
    display: grid
    &__item:not(:last-child)
        margin-bottom: 1.5rem
        
.item
    display: flex
    &__statement
        margin-right: 1rem